<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import storage from 'store'
export default {
  name: 'App',
  data () {
    return {
      baseUrl: '',
      token: ''
    }
  },
  mounted() {
    this.baseUrl = storage.get('local_dev')
    this.token = storage.get('Access-Token')
  },
  methods: {
    handleSet () {
      storage.set('local_dev', this.baseUrl)
    },
    handleSetToken () {
      storage.set('Access-Token', this.token)
    }
  }
}
</script>

<style>
@import "assets/css/reset-antd.css";
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height:100%;
}
::-webkit-scrollbar{
  width:6px;
  height: 6px;
}

::-webkit-scrollbar-thumb { /*滚动条里面小方块*/
  background: rgba(0,0,0,0.15);
  border-radius: 6px;
}
::-webkit-scrollbar-track{
  display: none;
}
</style>
