import Vue from 'vue'
import { Filter } from '@handday/components'
// base library
import {
  ConfigProvider,
  Layout,
  Input,
  InputNumber,
  Button,
  Switch,
  Radio,
  Checkbox,
  Select,
  Card,
  Form,
  Row,
  Col,
  Modal,
  Table,
  Tabs,
  Icon,
  Badge,
  Popover,
  Dropdown,
  List,
  Avatar,
  Breadcrumb,
  Steps,
  Spin,
  Menu,
  Drawer,
  Tooltip,
  Alert,
  Tag,
  Divider,
  DatePicker,
  TimePicker,
  Upload,
  Progress,
  Skeleton,
  Popconfirm,
  PageHeader,
  Result,
  Statistic,
  Descriptions,
  message,
  notification,
  FormModel,
  Pagination,
  Tree,
  AutoComplete
} from 'ant-design-vue'
import './directives'
import 'ant-design-vue/dist/antd.less'
message.config({
  top: `100px`,
  duration: 2,
  maxCount: 1
})
Spin.setDefaultIndicator({
  indicator: {
    render () {
      return <a-icon type="loading" style="font-size: 24px" spin />
    }
  }
})
Vue.use(Filter)
// ext library
Vue.use(ConfigProvider)
Vue.use(Layout)
Vue.use(Input)
